<template>
    <div class="myShop">
        
        <!-- 轮播图 -->
        <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
                <van-swipe-item v-for="(item,index) in bannerData" :key="index">
                    <img v-lazy="item" />
                    <span :class="index==0 ? 'hots' : 'hot'" @click="ff_under"></span>
                </van-swipe-item>
        </van-swipe>

        <div class="banText">
            <span class="iconfont" :class="item.calName" v-for="(item,index) in bannText" :key="index">{{item.title}}</span>
        </div>

        <van-grid :column-num="5">
             <van-grid-item v-for="(value,index) in bannZi" :key="index" icon="photo-o"  >
                  <van-image :src="value.src" />
                  <template>
                      <span>{{value.title}}</span>
                  </template>
             </van-grid-item>
        </van-grid>


        <div class="ings">
            <img src="../assets/img/1598958391.gif"/>
        </div>

        <div class="miao">
            <div class="heard">
                <span>限时秒杀</span>
                <van-count-down :time="time">
                        <template #default="timeData">
                            <span class="block">{{ timeData.hours }}</span>
                            <span class="colon">:</span>
                            <span class="block">{{ timeData.minutes }}</span>
                            <span class="colon">:</span>
                            <span class="block">{{ timeData.seconds }}</span>
                        </template>
                </van-count-down>
            </div>
            <div class="bodys">
                 <div class="win">
                       <div class="goods" v-for="(item,index) in banDate" :key="index">
                           <img :src="item.src"/>
                           <div class="goodBody">
                               <p>{{item.title}}</p>
                               <p class="miaos">
                                   <span>￥{{item.price}}</span>
                                   <span>￥{{item.priceX}}</span>
                               </p>
                               <p>立即抢购</p>
                           </div>
                       </div>
                 </div>
            </div>
        </div>


        
    </div>
</template>

<script>
    import '../assets/less/Shop.less';
    import '../assets/font/iconfont.css'
    export default {
        name:'Shop',
        data() {
            return {
                bannerData : [
                    require('../assets/img/bann_01.png'),
                    require('../assets/img/bann_02.png'),
                    require('../assets/img/bann_03.png'),
                    require('../assets/img/bann_04.png'),
                    require('../assets/img/bann_05.png'),
                    require('../assets/img/bann_06.png'),
                    require('../assets/img/bann_07.png'),
                ],
                bannText   : [
                    {calName : 'icon-icon_ruixingkafei1',title : '瑞幸甄选'},
                    {calName : 'icon-anquan'            ,title : '品质保证'},
                    {calName : 'icon-coin'              ,title : '7天无忧售后'},
                ],
                bannZi     : [
                    {src : require('../assets/img/zi_01.png'),title : '新品首发'},
                    {src : require('../assets/img/zi_02.png'),title : '咖啡周边'},
                    {src : require('../assets/img/zi_03.png'),title : '冲调茶饮'},
                    {src : require('../assets/img/zi_04.png'),title : '休闲零食'},
                    {src : require('../assets/img/zi_05.png'),title : '家居日用'},
                    {src : require('../assets/img/zi_06.png'),title : '舒适办公'},
                    {src : require('../assets/img/zi_07.png'),title : '美妆个护'},
                    {src : require('../assets/img/zi_08.png'),title : '数码配件'},
                    {src : require('../assets/img/zi_09.png'),title : '生活电器'},
                    {src : require('../assets/img/zi_10.png'),title : '潮玩服饰'},
                ],
                time       : 86400000,
                banDate    : [
                    {src : require('../assets/img/a_01.png'),title : '拍立得 时尚相机',price:130,priceX:146},
                    {src : require('../assets/img/a_02.png'),title : '日系 铅笔桶'    ,price:130,priceX:146},
                    {src : require('../assets/img/a_03.png'),title : '热水袋'         ,price:130,priceX:146},
                    {src : require('../assets/img/a_04.png'),title : '神奇的猫抓杯'   ,price:130,priceX:146},
                    {src : require('../assets/img/a_05.png'),title : '情侣保温ping'   ,price:130,priceX:146},
                    {src : require('../assets/img/a_06.png'),title : '舒适办公'       ,price:130,priceX:146},
                ]

            }
        },    
        methods: {
            ff_under(){
                this.$toast.loading({
                    message: '   维护中...  ',
                    forbidClick: true,
                    loadingType: 'spinner',
                });
            }
        },
    }
</script>

